export function lineScaleUp(data, timeUnit, offset){
    if(data.type === 'line' || data.type === 'area') {
        data.element.animate({
          d: {
            begin: offset,
            dur: timeUnit*data.index,
            from: data.path.clone().scale(1, 0)
            .translate(0, data.chartRect.height())
            .stringify(),
            to: data.path.clone().stringify(),
            easing: 'easeOutQuart'
          }
        });
    }
}

export function heightUp(animationObj, data){
    animationObj['y1'] = {
        begin: data.index * 50,
        dur: 500,
        from: data['y2'],
        to: data['y1'],
        easing: 'easeOutQuart'
    }
    return animationObj;
}


