export const SignIn = (isSignedIn) => {
    return new Promise((resolve, reject) => {
        try{
            if(!isSignedIn){
                console.log("promise signing in");
                let res = gapi.auth2.getAuthInstance().signIn()
                .then(()=>{
                    console.log("promise signed in ");
                    resolve();
                })
                .catch(e => {
                    console.log("promise error signing in " + e);
                });
            }else{
                console.log("promise already signed in ");
                resolve();
            }
        }catch(error){
            console.log("sign in error");
            reject(error);
        }
    })
};

export const requestPermissions = (scopes) => {
    // var options = new gapi.auth2.SigninOptionsBuilder(
    //     {'scope': 'email https://www.googleapis.com/auth/drive'});
    var options = new gapi.auth2.SigninOptionsBuilder(scopes);
    return new Promise((resolve, reject) => {
        console.log("user:");
        console.log(gapi);
        //let googleUser = auth2.currentUser.get();
        let googleUser = gapi.auth2.getAuthInstance().currentUser.get();
        //this obj has property grantOfflineAccess and isSignedIn!
        console.log("google user");
        console.log(googleUser);
        googleUser.grant(options)
        .then((res) => {
            console.log("promise scopes success");
            console.log(res);
            resolve();
        })
        .catch(rej => {
            console.log("promise scopes failure -> " + rej);
            console.log(rej);
            reject();
            //return{error: "popup_closed_by_user"} if i click out of it without response
                //clicking cancel does the same
            //click view app info link results in no response?
        })
    })
}


