import React from 'react'
import styled from "styled-components";
import Button from './../UI/Button.jsx';
import Icon from './../UI/Icon.jsx';
import {connect} from 'react-redux';
import {setSignedIn} from './actions.js';
import {bindActionCreators} from 'redux';
import img from './../../assets/icons/svg/baseline-settings-20px.svg';
import {SignIn} from './utilities.js';


class SignInButton extends React.Component{
    constructor(props){
        super(props);
        this.handleAuthClick = this.handleAuthClick.bind(this);
        this.handleSignoutClick = this.handleSignoutClick.bind(this);
    }
    handleAuthClick(event) {
        //console.log("signing in")
        //gapi.auth2.getAuthInstance().signIn();
        //console.log(gapi.auth2.getAuthInstance());
        SignIn(this.props.gapi.isSignedIn)
        .then(r => {
            console.log("promise then sign in then " + r);
            return r;
        })
        .catch((e) => {
            console.log("promise catch sign in prmise catch error " + e);
        })
    }
    handleSignoutClick(event) {
        console.log("signing out")
        gapi.auth2.getAuthInstance().signOut();
       // this.props.setSignedIn(false);
    }
    renderButton(){
        if(this.props.gapi.isSignedIn){
            return <Button onClick={()=>{this.handleSignoutClick()}}>Sign Out</Button>
        }else{
            return <Button onClick={()=>{this.handleAuthClick()}}>Sign In</Button>
            
        }
    }

    render(){
        // console.log("signed in? " + this.props.gapi.isSignedIn)
        // console.log(this.props);
        return (
            this.renderButton()
            // <Container>
            //     {/* <Icon  src={img} alt="my icon"/> */}
            //     <Icon icon={img} />
            // </Container>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        gapi: state.gapi
    };
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
            setSignedIn
        }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(SignInButton);

const Container = styled.div`
    position: absolute;
    top:0;
    right:0;
    padding: 10px;
`
// const Icon = styled.img`
//     font-size: 48px;
//     width: 48px;
// `


