import React from 'react';
import ReactDOM from 'react-dom';
import ChartistGraph from 'react-chartist';
import styled from "styled-components";
import {lineScaleUp, heightUp} from './animations.js';
import './styles.css';
// import Chartist from 'chartist';
 
export default class Pie extends React.Component {
  constructor(props){
    super(props);
    var type = 'Line'
    var options = {
      showPoint: false,
      axisX: {
        position: 'start',
        offset: 60
      },

    };
    var seq = 0,
  delays = 80,
  durations = 500;
    var listeners={
      created: function() {
        var seq = 0;
      },
      draw: function(data){
       // console.log("animating data, seq " + seq);
      //  console.log(data);
        seq++;
        if(data.type === 'line') {
          lineScaleUp(data, 1000, 500);
        }else if(data.type === 'grid') {
          
          if(data.axis.units.pos === 'x'){
            var pos1Animation = {
              begin: data.index * 50,
              dur: durations,
              from: data[data.axis.units.pos + '1'] - 30,
              to: data[data.axis.units.pos + '1'],
              easing: 'easeOutQuart'
            };
        
            var pos2Animation = {
              begin: data.index * 50,
              dur: durations,
              from: data[data.axis.units.pos + '2'] - 100,
              to: data[data.axis.units.pos + '2'],
              easing: 'easeOutQuart'
            };

            // var heightAnim = {
            //   begin: data.index * 50,
            //   dur: durations,
            //   from: data['y2'],
            //   to: data['y1'],
            //   easing: 'easeOutQuart'
            // }
        
            var animations = {};
            // animations[data.axis.units.pos + '1'] = pos1Animation;
            // animations[data.axis.units.pos + '2'] = pos2Animation;
            //animations['y1'] = heightAnim;
            heightUp(animations, data);
            animations['opacity'] = {
              begin: data.index * 50,
              dur: durations,
              from: 0,
              to: 1,
              easing: 'easeOutQuart'
            };
        
            data.element.animate(animations);
          }
          
        }
      }
    }
    //let chart = <ChartistGraph  data={props.data} listener={listeners} options={props.options} type={'Line'} />;
 //   console.log("line chart");
 //   console.log(chart);
    this.state={
      type,
      listeners,
      options
    }
  }
  render() {

 
    return (
      <Container className="chartContainer">
        <ChartistGraph  data={this.props.data} listener={this.state.listeners} options={this.props.options} type={'Line'} />
        {/* <ChartistGraph data={this.props.data} options={this.props.options} type={type} /> */}
      </Container>
    )
  }
}

const Container = styled.div`

`


