import React from "react";
import Link from 'gatsby-link';

import styled from "styled-components";
import ReactGA from 'react-ga';
// import PostList from './../components/Blog/PostList.js';
import PostListing from './../components/Blog/PostListing.jsx';
import Button from './../components/UI/Button.jsx';
import SignInBtn from './../components/Google/SignInButton.jsx';

import FadeIn from './../components/UI/animations/FadeIn.jsx';
import ListIn from './../components/UI/animations/ListIn.jsx';
import AnimList from './../components/UI/animations/TestAnim.jsx';

import Line from './../components/graphs/Line.jsx';
import TrendsView from './../components/graphs/views/Trends.jsx';
import Bg from './../components/UI/Background.jsx';

const IndexPage = ({
  data,
}) => {
  const { allContentJson } = data; // data.markdownRemark holds our post data
  const nodes = allContentJson.edges;
  console.log("index nodes");
  console.log(nodes);
  const posts = nodes.map(node => {
    return <p key={node.node.id} >
    <Link key={node.node.id} to={node.node.path}>Post: {node.node.value}</Link>
    </p>
  })

  return (
    <FadeIn>
      <Bg>
        <TrendsView />
      </Bg>
      <Container>

         <Box>
           <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. </p>
        </Box>
        <ListIn>
          {nodes.map(p => {
                  let {path, tags, title, date} = p.node;
                  return (
                      <PostListing key={path} post={p.node}/>
                  )
          })}
        </ListIn>
      </Container>
    </FadeIn>
  )
;
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexQuery {
    site{
      siteMetadata{
        title
      }
    },
    allContentJson{
      edges {
        node {
          title,
          date,
          path,
          tags,
          game,
          content{
            type, text, options
          }
        }
      }
    }
  }
`;


const Box = styled.div`
  width:100%
  border: 2px white solid;
  color: white;
  padding: 10px
`

const Container = styled.div`
  background: rgba(255,255,255,0.5);
  width: 50%;
  padding: 20px;
  position: relative; 
  left: 70px; 
  &:last-child div:last-child{
    margin-bottom:0px;
  }
`

