export default UiIcon;


const Icon = styled.img`
    font-size: 36px;
    width: 36px;
    color: blue;
    transition: all ${timing.mini}s ease-out;
    border-bottom: 2px white solid;
    &:hover{
        border-bottom: 2px ${colors.accentShadow} solid;
    }
`

